@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family:'poppins', sans-serif;
}
/*Menu de navegaci√≥n*/
.nav{
  display: flex;
  position: fixed;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 12vh;
  background-color: black;
}
.nav__logo{
  color: white;
  font-size: 28px;
  font-weight: bold;
  padding-left: 15px;
}
.menu{
  display: flex;
}
.menu li{
  color: white;
  list-style: none;
  margin: 0 15px 0 15px;
}
/*404 NOT FOUND STYLES*/
.not_found{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-color: black;
}
.minion__found{
  width: 450px;
  height: 300px;
}
.found__center{
  text-align: center;
}
.title__found{
  color: white;
  font-size: 60px;
}
.subtitle__found{
  font-size: 45px;
  color: white;
}
/*Menu del login*/
.title{
  color: black
}
.form{
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 10px;
  background: gray;
  border-radius: 15px;
  margin-bottom: 30px;
}
.input{
  margin-bottom : 10px;
  padding : 5px;
}
.button{
  background-color: #fff;
  color : black;
  margin-bottom: 10px;
}
.logo {
  height: 6em;
  padding: 1.5em;
  will-change: filter;
}
.logo:hover {
  filter: drop-shadow(0 0 2em #646cffaa);
}
.logo.react:hover {
  filter: drop-shadow(0 0 2em #61dafbaa);
}

@keyframes logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (prefers-reduced-motion: no-preference) {
  a:nth-of-type(2) .logo {
    animation: logo-spin infinite 20s linear;
  }
}

.card {
  padding: 2em;
}

.read-the-docs {
  color: #888;
}